export default new Vuetify({
  theme: {
      options: {
        customProperties: true,
      },
    themes: {
      light: {
        primary: '#20BF9C',
        secondary: '#0FAF7C',
        accent: '#20BFDC',
        info: '#009CCC',
        error: '#FF5252',
        success: '#4CAF50',
        warning: '#FFC107'
      },
    },
  },
});
